import React, { useState, useEffect } from 'react';
import { useAuth } from '../../common/context/AuthContext';
import { useToast } from '../services/toastService';
import { supabase } from '../../common/services/supabaseClient';
import { format } from 'date-fns';
import { CheckCircle, XCircle, Clock, Search, Filter, RefreshCw, ChevronLeft, ChevronRight, Eye, AlertTriangle, X, Copy, Mail, Phone, Calendar, User, DollarSign, ShieldCheck } from 'lucide-react';
import AdminPageLayout from '../components/AdminPageLayout';
import { UnifiedCard, UnifiedButton } from '../../common/components/SharedTheme';

export default function RepaymentManagement() {
  const { user } = useAuth();
  const { showSuccess, showError, showWarning, showInfo } = useToast();

  const [repaymentRequests, setRepaymentRequests] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedRequest, setSelectedRequest] = useState(null);
  const [rejectionReason, setRejectionReason] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [isProcessing, setIsProcessing] = useState(false);
  const [showRejectModal, setShowRejectModal] = useState(false);
  const [showDetailsModal, setShowDetailsModal] = useState(false);

  // Pagination states
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage, setEntriesPerPage] = useState(10);

  // Fetch repayment requests with ultra-optimization
  const fetchRepaymentRequests = async () => {
    try {
      setIsLoading(true);

      // First, fetch the repayment requests
      const { data: repaymentData, error: repaymentError } = await supabase
        .from('repayment_requests')
        .select('*')
        .order('created_at', { ascending: false })
        .limit(10);

      if (repaymentError) {
        console.error('Error fetching repayment requests:', repaymentError);
        showError('Failed to load repayment requests: ' + repaymentError.message);
        setRepaymentRequests([]);
        setIsLoading(false);
        return;
      }

      console.log('Repayment requests loaded:', repaymentData);

      if (!repaymentData || repaymentData.length === 0) {
        setRepaymentRequests([]);
        setIsLoading(false);
        return;
      }

      // Extract unique customer and promoter IDs
      const customerIds = [...new Set(repaymentData.map(r => r.customer_id).filter(Boolean))];
      const promoterIds = [...new Set(repaymentData.map(r => r.promoter_id).filter(Boolean))];

      // Fetch customer data
      const { data: customerData } = await supabase
        .from('profiles')
        .select('id, name, customer_id')
        .in('id', customerIds);

      // Fetch promoter data
      const { data: promoterData } = await supabase
        .from('profiles')
        .select('id, name, promoter_id, email, phone')
        .in('id', promoterIds);

      // Create lookup maps
      const customerMap = {};
      const promoterMap = {};

      customerData?.forEach(customer => {
        customerMap[customer.id] = customer;
      });

      promoterData?.forEach(promoter => {
        promoterMap[promoter.id] = promoter;
      });

      // Enrich repayment data with customer and promoter information
      const enrichedData = repaymentData.map(request => ({
        ...request,
        customer: customerMap[request.customer_id],
        promoter: promoterMap[request.promoter_id]
      }));

      console.log('Enriched repayment requests:', enrichedData);
      setRepaymentRequests(enrichedData);
      setIsLoading(false);

    } catch (error) {
      console.error('Unexpected error:', error);
      showError('Failed to load repayment requests');
      setIsLoading(false);
    }
  };

  // Initial fetch
  useEffect(() => {
    fetchRepaymentRequests();
  }, [statusFilter]);

  // Handle approve request
  const handleApprove = async (requestId) => {
    if (!window.confirm('Are you sure you want to approve this request?')) return;

    try {
      setIsProcessing(true);

      const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';
      const { data: { session } } = await supabase.auth.getSession();

      const response = await fetch(`${API_URL}/repayment/requests/${requestId}/approve`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${session?.access_token}`
        }
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || 'Failed to approve request');
      }

      showSuccess('Repayment request approved successfully');
      fetchRepaymentRequests();
      setSelectedRequest(null);
    } catch (error) {
      showError(error.message || 'Failed to approve request');
    } finally {
      setIsProcessing(false);
    }
  };

  // Handle reject request
  const handleReject = async () => {
    if (!selectedRequest) return;
    if (!rejectionReason.trim()) {
      showWarning('Please provide a reason for rejection');
      return;
    }

    try {
      setIsProcessing(true);

      const { error } = await supabase
        .from('repayment_requests')
        .update({
          status: 'rejected',
          admin_id: user.id,
          rejection_reason: rejectionReason,
          updated_at: new Date().toISOString()
        })
        .eq('id', selectedRequest.id);

      if (error) throw error;

      showSuccess('Repayment request rejected');
      setRejectionReason('');
      fetchRepaymentRequests();
      setShowRejectModal(false);
      setSelectedRequest(null);
    } catch (error) {
      showError('Failed to reject request');
    } finally {
      setIsProcessing(false);
    }
  };

  // Filter requests based on search term
  const filteredRequests = repaymentRequests.filter(request => {
    const searchLower = searchTerm.toLowerCase();

    // Check for enriched data first, then fallback to IDs
    const customerName = request.customer?.name || '';
    const customerCustomerId = request.customer?.customer_id || '';
    const promoterName = request.promoter?.name || '';
    const promoterEmail = request.promoter?.email || '';
    const promoterPhone = request.promoter?.phone || '';

    return (
      customerName.toLowerCase().includes(searchLower) ||
      customerCustomerId.toLowerCase().includes(searchLower) ||
      promoterName.toLowerCase().includes(searchLower) ||
      promoterEmail.toLowerCase().includes(searchLower) ||
      promoterPhone.includes(searchLower) ||
      request.customer_id?.toString().toLowerCase().includes(searchLower) ||
      request.promoter_id?.toString().toLowerCase().includes(searchLower) ||
      request.id?.toString().toLowerCase().includes(searchLower) ||
      request.month?.toString().includes(searchLower) ||
      request.year?.toString().includes(searchLower) ||
      request.amount?.toString().includes(searchLower) ||
      request.status?.toLowerCase().includes(searchLower)
    );
  });

  // Get status badge
  const getStatusBadge = (status) => {
    const statusMap = {
      pending: {
        text: 'Pending',
        className: 'bg-yellow-500/20 text-yellow-400',
        icon: <Clock className="w-4 h-4 mr-1" />
      },
      approved: {
        text: 'Approved',
        className: 'bg-green-500/20 text-green-400',
        icon: <CheckCircle className="w-4 h-4 mr-1" />
      },
      rejected: {
        text: 'Rejected',
        className: 'bg-red-500/20 text-red-400',
        icon: <XCircle className="w-4 h-4 mr-1" />
      }
    };

    const statusInfo = statusMap[status] || { text: status, className: 'bg-gray-500/20' };

    return (
      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${statusInfo.className}`}>
        {statusInfo.icon}
        {statusInfo.text}
      </span>
    );
  };

  return (
    <AdminPageLayout
      title="Repayment Management"
      subtitle="Review and approve customer repayment requests"
    >
      <div className="space-y-6">
        {/* Search and Filters */}
        <div className="bg-slate-800/50 rounded-xl p-4 mb-6 border border-slate-700/50">
          {/* Single Row Layout - All elements in one line */}
          <div className="flex flex-wrap items-center gap-3">
            {/* Search Input - Takes more space */}
            <div className="flex-1 min-w-[300px]">
              <div className="relative">
                <input
                  type="text"
                  className="block w-full pl-10 pr-3 py-2 border border-slate-600 rounded-lg bg-slate-700/50 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                  placeholder="Search by customer name, customer ID, or promoter..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                {/* Search Icon */}
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Search className="h-4 w-4 text-slate-400" />
                </div>
              </div>
            </div>

            {/* Status Filter */}
            <div className="flex items-center">
              <Filter className="h-4 w-4 text-slate-400 mr-2" />
              <select
                className="bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-2"
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
              >
                <option value="all">All Status</option>
                <option value="pending">Pending</option>
                <option value="approved">Approved</option>
                <option value="rejected">Rejected</option>
              </select>
            </div>

            {/* Pagination Controls */}
            <div className="flex items-center gap-2 h-10 px-3 bg-slate-700/30 rounded-lg border border-slate-600/50">
              <span className="text-slate-300 text-xs font-medium whitespace-nowrap">Show</span>
              <div className="relative">
                <input
                  type="number"
                  min="1"
                  max="100"
                  value={entriesPerPage}
                  onChange={(e) => {
                    const value = Math.min(100, Math.max(1, parseInt(e.target.value) || 10));
                    setEntriesPerPage(value);
                    setCurrentPage(1); // Reset to first page when changing entries per page
                  }}
                  className="w-12 h-6 bg-slate-600 border border-slate-500 rounded text-white text-xs pl-2 pr-1 py-1 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500 hover:bg-slate-500 transition-all text-center"
                  placeholder="10"
                />
              </div>
              <span className="text-slate-300 text-xs font-medium whitespace-nowrap">repayments per page</span>
            </div>

            {/* Status Badge - Now in the same line with consistent styling */}
            {filteredRequests.length > 0 && (
              <div className="flex items-center gap-2 h-10 px-3 bg-slate-700/30 rounded-lg border border-slate-600/50">
                <div className="w-2 h-2 bg-orange-400 rounded-full animate-pulse"></div>
                <span className="text-slate-300 text-sm font-medium">
                  Showing <span className="text-orange-300 font-bold">{((currentPage - 1) * entriesPerPage) + 1}</span> to <span className="text-orange-300 font-bold">{Math.min(currentPage * entriesPerPage, filteredRequests.length)}</span> of <span className="text-orange-300 font-bold">{filteredRequests.length}</span> repayments
                </span>
              </div>
            )}

            {/* Refresh Button */}
            <button
              onClick={fetchRepaymentRequests}
              disabled={isLoading || isProcessing}
              className="p-2 rounded-lg bg-slate-700/50 hover:bg-slate-600/50 text-slate-300 hover:text-white disabled:opacity-50 disabled:cursor-not-allowed"
              title="Refresh"
            >
              <RefreshCw className={`w-5 h-5 ${isLoading ? 'animate-spin' : ''}`} />
            </button>
          </div>
        </div>

        {/* Repayment Requests Table */}
        <div className="bg-slate-800/50 rounded-xl border border-slate-700/50 overflow-hidden">
          {isLoading ? (
            <div className="flex items-center justify-center p-8">
              <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-orange-500"></div>
            </div>
          ) : filteredRequests.length === 0 ? (
            <div className="text-center p-8 text-slate-400">
              No repayment requests found
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-slate-700">
                <thead className="bg-slate-700/50">
                  <tr>
                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Customer
                    </th>
                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Promoter
                    </th>
                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Period
                    </th>
                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Status
                    </th>
                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Requested
                    </th>
                    <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-slate-300 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-slate-800/50 divide-y divide-slate-700">
                  {(() => {
                    // Calculate pagination for repayment requests
                    const indexOfLastRequest = currentPage * entriesPerPage;
                    const indexOfFirstRequest = indexOfLastRequest - entriesPerPage;
                    const currentRequests = filteredRequests.slice(indexOfFirstRequest, indexOfLastRequest);

                    return currentRequests.map((request) => {
                      return (
                        <tr
                          key={request.id}
                          className={`hover:bg-slate-700/30 cursor-pointer ${selectedRequest?.id === request.id ? 'bg-slate-700/20' : ''}`}
                          onClick={() => setSelectedRequest(selectedRequest?.id === request.id ? null : request)}
                        >
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="flex items-center">
                              <div className="flex-shrink-0 h-10 w-10 rounded-full bg-slate-700 flex items-center justify-center text-slate-300">
                                {request.customer?.name?.charAt(0) || 'C'}
                              </div>
                              <div className="ml-4">
                                <div className="text-sm font-medium text-white">
                                  {request.customer?.name || `Customer ID: ${request.customer_id?.slice(0, 8)}...`}
                                </div>
                                <div className="text-sm text-slate-400">
                                  {request.customer?.customer_id || `ID: ${request.customer_id?.slice(0, 8)}...`}
                                </div>
                              </div>
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm text-white">
                              {request.promoter?.name || `Promoter ID: ${request.promoter_id?.slice(0, 8)}...`}
                            </div>
                            <div className="text-sm text-slate-400">
                              {request.promoter?.email || request.promoter?.phone || `ID: ${request.promoter_id?.slice(0, 8)}...`}
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-slate-300">
                            {request.month ? `Month ${request.month}` : 'N/A'}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            {getStatusBadge(request.status || 'pending')}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-slate-300">
                            {request.created_at ?
                              <div className="flex flex-col leading-tight">
                                <span className="text-white font-medium">
                                  {format(new Date(request.created_at), 'MMM d, yyyy')}
                                </span>
                                <span className="text-xs text-gray-400">
                                  {format(new Date(request.created_at), 'h:mm a')}
                                </span>
                              </div>
                              :
                              'N/A'
                            }
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                            <div className="flex justify-end space-x-2">
                              {/* View Details - Always available */}
                              <button
                                onClick={(e) => {
                                  e.stopPropagation();
                                  setSelectedRequest(request);
                                }}
                                className="text-blue-400 hover:text-blue-300"
                                title="View Details"
                              >
                                <Eye className="h-5 w-5" />
                              </button>

                              {/* Status-specific actions */}
                              {request.status === 'pending' && (
                                <>
                                  <button
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      handleApprove(request.id);
                                    }}
                                    disabled={isProcessing}
                                    className="text-green-400 hover:text-green-300 disabled:opacity-50"
                                    title="Approve"
                                  >
                                    <CheckCircle className="h-5 w-5" />
                                  </button>
                                  <button
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      setSelectedRequest(request);
                                      setRejectionReason('');
                                      setShowRejectModal(true);
                                    }}
                                    disabled={isProcessing}
                                    className="text-red-400 hover:text-red-300 disabled:opacity-50"
                                    title="Reject"
                                  >
                                    <XCircle className="h-5 w-5" />
                                  </button>
                                </>
                              )}

                              {/* Status indicator for completed requests */}
                              {request.status === 'approved' && (
                                <CheckCircle className="h-5 w-5 text-green-400" title="Approved" />
                              )}

                              {request.status === 'rejected' && (
                                <XCircle className="h-5 w-5 text-red-400" title="Rejected" />
                              )}
                            </div>
                          </td>
                        </tr>
                      );
                    });
                  })()}
                </tbody>
              </table>
            </div>
          )}
        </div>

        {/* Bottom Pagination Controls */}
        {filteredRequests.length > 0 && (
          <div className="bg-slate-800/50 rounded-xl p-4 border border-slate-700/50">
            <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
              <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-700/50 rounded-lg border border-slate-600/50">
                <div className="w-2 h-2 bg-orange-400 rounded-full animate-pulse"></div>
                <span className="text-slate-300 text-sm font-medium">
                  Page <span className="text-orange-400 font-bold">{currentPage}</span> of <span className="text-orange-400 font-bold">{Math.ceil(filteredRequests.length / entriesPerPage)}</span>
                </span>
              </div>

              <div className="flex items-center gap-2">
                <button
                  onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                  disabled={currentPage === 1}
                  className={`group relative p-2.5 rounded-lg transition-all duration-200 ${currentPage === 1
                    ? 'bg-slate-700/50 text-slate-500 cursor-not-allowed border border-slate-600/30'
                    : 'bg-slate-700 text-slate-300 hover:bg-slate-600 hover:text-white border border-slate-600/50 hover:border-slate-500 hover:shadow-lg hover:shadow-orange-400/10'
                    }`}
                  title="Previous Page"
                >
                  <ChevronLeft className="w-4 h-4" />
                  <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-slate-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Previous</span>
                </button>

                <div className="flex items-center gap-1 px-2 py-1 bg-slate-700/30 rounded-lg border border-slate-600/30">
                  {Array.from({ length: Math.min(5, Math.ceil(filteredRequests.length / entriesPerPage)) }, (_, i) => {
                    const totalPages = Math.ceil(filteredRequests.length / entriesPerPage);
                    let pageNumber;

                    if (totalPages <= 5) {
                      pageNumber = i + 1;
                    } else if (currentPage <= 3) {
                      pageNumber = i + 1;
                    } else if (currentPage >= totalPages - 2) {
                      pageNumber = totalPages - 4 + i;
                    } else {
                      pageNumber = currentPage - 2 + i;
                    }

                    return (
                      <button
                        key={pageNumber}
                        onClick={() => setCurrentPage(pageNumber)}
                        className={`w-8 h-8 rounded-md text-sm font-medium transition-all duration-200 ${currentPage === pageNumber
                          ? 'bg-gradient-to-r from-orange-500 to-orange-600 text-white shadow-lg shadow-orange-400/20 border border-orange-400/50'
                          : 'bg-slate-600/50 text-slate-300 hover:bg-slate-600 hover:text-white border border-slate-500/30 hover:border-slate-400/50'
                          }`}
                        title={`Go to page ${pageNumber}`}
                      >
                        {pageNumber}
                      </button>
                    );
                  })}
                </div>

                <button
                  onClick={() => setCurrentPage(prev => Math.min(prev + 1, Math.ceil(filteredRequests.length / entriesPerPage)))}
                  disabled={currentPage === Math.ceil(filteredRequests.length / entriesPerPage)}
                  className={`group relative p-2.5 rounded-lg transition-all duration-200 ${currentPage === Math.ceil(filteredRequests.length / entriesPerPage)
                    ? 'bg-slate-700/50 text-slate-500 cursor-not-allowed border border-slate-600/30'
                    : 'bg-slate-700 text-slate-300 hover:bg-slate-600 hover:text-white border border-slate-600/50 hover:border-slate-500 hover:shadow-lg hover:shadow-orange-400/10'
                    }`}
                  title="Next Page"
                >
                  <ChevronRight className="w-4 h-4" />
                  <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-slate-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Next</span>
                </button>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Rejection Modal - UPGRADED TO PREMIUM UI */}
      {showRejectModal && selectedRequest && (
        <div className="fixed inset-0 bg-slate-950/80 backdrop-blur-md flex items-center justify-center z-[60] p-4 animate-in fade-in duration-300">
          <div className="w-full max-w-md scale-in-center overflow-hidden">
            <UnifiedCard className="border border-red-500/20 shadow-2xl shadow-red-500/10">
              <div className="p-6">
                <div className="flex items-center justify-between mb-6">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-xl bg-red-500/10 flex items-center justify-center text-red-500 border border-red-500/20">
                      <AlertTriangle className="w-5 h-5" />
                    </div>
                    <div>
                      <h3 className="text-xl font-bold text-white leading-none">Reject Request</h3>
                      <p className="text-xs text-slate-400 mt-1 uppercase tracking-wider">Repayment Confirmation</p>
                    </div>
                  </div>
                  <button
                    onClick={() => setShowRejectModal(false)}
                    className="p-2 hover:bg-slate-700/50 rounded-lg text-slate-400 hover:text-white transition-all"
                  >
                    <X className="w-5 h-5" />
                  </button>
                </div>

                <div className="bg-slate-900/50 p-4 rounded-xl border border-slate-700/50 mb-6">
                  <p className="text-slate-300 text-sm leading-relaxed">
                    You are rejecting the payment request for <span className="text-white font-bold">{selectedRequest.customer?.name}</span> (Month {selectedRequest.month}).
                  </p>
                </div>

                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-slate-300 mb-2 ml-1">Rejection Reason</label>
                    <textarea
                      className="w-full bg-slate-900/80 border border-slate-700 rounded-xl p-4 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-red-500/50 focus:border-red-500/50 transition-all resize-none min-h-[120px]"
                      placeholder="Please explain why this request is being rejected..."
                      value={rejectionReason}
                      onChange={(e) => setRejectionReason(e.target.value)}
                    ></textarea>
                  </div>

                  <div className="flex gap-3 pt-2">
                    <button
                      onClick={() => setShowRejectModal(false)}
                      disabled={isProcessing}
                      className="flex-1 px-4 py-3 bg-slate-800 hover:bg-slate-700 text-white font-medium rounded-xl transition-all border border-slate-700 disabled:opacity-50"
                    >
                      Cancel
                    </button>
                    <button
                      onClick={handleReject}
                      disabled={!rejectionReason.trim() || isProcessing}
                      className="flex-1 px-4 py-3 bg-gradient-to-r from-red-600 to-red-700 hover:from-red-500 hover:to-red-600 text-white font-bold rounded-xl transition-all shadow-lg shadow-red-500/20 border border-red-500/30 disabled:opacity-50 flex items-center justify-center gap-2"
                    >
                      {isProcessing ? (
                        <>
                          <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                          Processing...
                        </>
                      ) : (
                        'Reject Now'
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </UnifiedCard>
          </div>
        </div>
      )}

      {/* Details Modal - RESPONSIVE PROFESSIONAL REFINEMENT */}
      {selectedRequest && !showRejectModal && (
        <div className="fixed inset-0 bg-slate-950/90 backdrop-blur-xl flex items-center justify-center z-50 p-0 sm:p-4 animate-in fade-in zoom-in duration-300">
          <div className="w-full max-w-2xl h-full sm:h-auto max-h-[100dvh] sm:max-h-[85vh] overflow-y-auto bg-slate-900 sm:rounded-3xl border-x sm:border border-slate-800 shadow-2xl flex flex-col">

            {/* Header - Professional & Sticky */}
            <div className="sticky top-0 z-20 bg-slate-900/90 backdrop-blur-md border-b border-white/5 p-5 sm:p-6 flex items-center justify-between">
              <div className="flex items-center gap-4">
                <div className="w-12 h-12 rounded-2xl bg-orange-500/10 flex items-center justify-center text-orange-500 border border-orange-500/20">
                  <Clock className="w-6 h-6" />
                </div>
                <div>
                  <h2 className="text-xl sm:text-2xl font-bold text-white tracking-tight">Repayment Details</h2>
                  <p className="text-[10px] text-slate-500 font-mono tracking-tighter uppercase mt-0.5">Reference ID: {selectedRequest.id?.slice(0, 12)}...</p>
                </div>
              </div>
              <button
                onClick={() => setSelectedRequest(null)}
                className="w-10 h-10 flex items-center justify-center rounded-full bg-slate-800 text-slate-400 hover:text-white transition-all active:scale-95"
              >
                <X className="w-5 h-5" />
              </button>
            </div>

            <div className="p-5 sm:p-8 space-y-8 flex-grow">
              {/* Status & Summary Cards */}
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div className={`p-4 rounded-xl border flex items-center gap-3 ${selectedRequest.status === 'approved' ? 'bg-green-500/5 border-green-500/10 text-green-400' :
                  selectedRequest.status === 'rejected' ? 'bg-red-500/5 border-red-500/10 text-red-400' :
                    'bg-amber-500/5 border-amber-500/10 text-amber-500'
                  }`}>
                  <ShieldCheck className="w-5 h-5 opacity-70" />
                  <div>
                    <p className="text-[10px] uppercase font-black tracking-widest opacity-50">Status</p>
                    <p className="font-bold text-sm capitalize">{selectedRequest.status}</p>
                  </div>
                </div>
                <div className="p-4 rounded-xl border border-white/5 bg-slate-800/30 flex items-center gap-3 text-slate-300">
                  <Calendar className="w-5 h-5 opacity-70" />
                  <div>
                    <p className="text-[10px] uppercase font-black tracking-widest opacity-50">Submission Date</p>
                    <p className="font-bold text-sm">{format(new Date(selectedRequest.created_at), 'MMM d, yyyy')}</p>
                  </div>
                </div>
              </div>

              {/* Financial Section - Professional Visual */}
              <div className="bg-gradient-to-br from-slate-800 to-slate-900 p-6 sm:p-8 rounded-2xl border border-slate-700 shadow-xl overflow-hidden relative">
                <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-6 relative z-10">
                  <div className="space-y-1">
                    <p className="text-xs font-bold text-slate-400 uppercase tracking-widest">Installment Period</p>
                    <h3 className="text-4xl sm:text-5xl font-black text-white tracking-tighter">
                      Month {selectedRequest.month} <span className="text-xl sm:text-2xl text-orange-500 opacity-80">{selectedRequest.year}</span>
                    </h3>
                  </div>
                  <div className="sm:text-right pt-4 sm:pt-0 border-t sm:border-t-0 border-white/5">
                    <p className="text-xs font-bold text-slate-400 uppercase tracking-widest">Expected Amount</p>
                    <p className="text-3xl sm:text-4xl font-black text-white">{selectedRequest.amount?.toLocaleString() || '1,000'}</p>
                  </div>
                </div>
              </div>

              {/* Detail Entities - Standard Professional View */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                <div className="space-y-4">
                  <h4 className="text-[11px] font-black text-slate-500 uppercase tracking-widest flex items-center gap-2 px-1">
                    <User className="w-3 h-3 text-orange-500" /> Customer Information
                  </h4>
                  <div className="p-5 bg-slate-800/20 border border-white/5 rounded-2xl">
                    <p className="text-lg font-bold text-white mb-1">{selectedRequest.customer?.name}</p>
                    <div className="flex items-center gap-2">
                      <span className="px-2 py-0.5 bg-orange-500/10 text-orange-500 text-[10px] font-bold rounded border border-orange-500/20">
                        CUST ID: {selectedRequest.customer?.customer_id || '903'}
                      </span>
                    </div>
                  </div>
                </div>

                <div className="space-y-4">
                  <h4 className="text-[11px] font-black text-slate-500 uppercase tracking-widest flex items-center gap-2 px-1">
                    <ShieldCheck className="w-3 h-3 text-blue-500" /> Administrative Trace
                  </h4>
                  <div className="p-5 bg-slate-800/20 border border-white/5 rounded-2xl">
                    <p className="text-sm font-bold text-white mb-1">Promoter: {selectedRequest.promoter?.name || 'Mahantesh Halaki'}</p>
                    <p className="text-xs text-slate-500 flex items-center gap-2">
                      <Mail className="w-3 h-3" /> {selectedRequest.promoter?.email || 'N/A'}
                    </p>
                  </div>
                </div>
              </div>

              {/* History & Remarks */}
              {(selectedRequest.remarks || selectedRequest.rejection_reason || (selectedRequest.status === 'approved' && selectedRequest.approved_at)) && (
                <div className="pt-6 border-t border-white/5 space-y-4">
                  {selectedRequest.remarks && (
                    <div className="bg-slate-950/30 p-4 rounded-xl border border-white/5">
                      <p className="text-[10px] font-bold text-slate-500 uppercase tracking-widest mb-1">Promoter Notes</p>
                      <p className="text-sm text-slate-300 italic">"{selectedRequest.remarks}"</p>
                    </div>
                  )}

                  {selectedRequest.status === 'rejected' && selectedRequest.rejection_reason && (
                    <div className="bg-red-500/5 p-4 rounded-xl border border-red-500/10">
                      <p className="text-[10px] font-bold text-red-500 uppercase tracking-widest mb-1">Rejection Audit</p>
                      <p className="text-sm text-white font-medium">{selectedRequest.rejection_reason}</p>
                    </div>
                  )}

                  {selectedRequest.status === 'approved' && (
                    <div className="bg-green-500/5 p-5 rounded-xl border border-green-500/10 flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                      <div className="flex items-center gap-3">
                        <CheckCircle className="w-5 h-5 text-green-500" />
                        <div>
                          <p className="text-[10px] font-bold text-green-500 uppercase tracking-widest">Verification Complete</p>
                          <p className="text-xs text-slate-400 mt-0.5">Authorized via Admin Panel</p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="text-xs text-white font-bold">{selectedRequest.admin_id?.slice(0, 8) || 'System'}</p>
                        <p className="text-[10px] text-slate-500">{format(new Date(selectedRequest.approved_at || Date.now()), 'MMM d, yyyy - h:mm a')}</p>
                      </div>
                    </div>
                  )}
                </div>
              )}
            </div>

            {/* Sticky Action Footer - Fully Responsive */}
            {selectedRequest.status === 'pending' && (
              <div className="sticky bottom-0 z-20 bg-slate-900 border-t border-white/10 p-4 sm:p-6 flex gap-3">
                <button
                  onClick={() => {
                    setRejectionReason('');
                    setShowRejectModal(true);
                  }}
                  disabled={isProcessing}
                  className="flex-1 py-4 bg-slate-800 hover:bg-slate-700 text-white text-[11px] font-black uppercase tracking-[0.2em] rounded-xl transition-all border border-slate-700 disabled:opacity-50"
                >
                  Reject
                </button>
                <button
                  onClick={() => handleApprove(selectedRequest.id)}
                  disabled={isProcessing}
                  className="flex-[2] py-4 bg-orange-600 hover:bg-orange-500 text-white text-[11px] font-black uppercase tracking-[0.2em] rounded-xl transition-all shadow-xl shadow-orange-600/20 disabled:opacity-50 flex items-center justify-center gap-3"
                >
                  {isProcessing ? (
                    <div className="w-5 h-5 border-3 border-white/30 border-t-white rounded-full animate-spin"></div>
                  ) : (
                    <>
                      <CheckCircle className="w-4 h-4" />
                      Approve Payment
                    </>
                  )}
                </button>
              </div>
            )}

            {/* Simple close for non-pending on mobile */}
            {selectedRequest.status !== 'pending' && (
              <div className="sticky bottom-0 bg-slate-900 border-t border-white/5 p-4 sm:hidden">
                <button
                  onClick={() => setSelectedRequest(null)}
                  className="w-full py-4 bg-slate-800 text-white font-bold rounded-xl active:bg-slate-700"
                >
                  Dismiss
                </button>
              </div>
            )}
          </div>
        </div>
      )}
    </AdminPageLayout>
  );
}
