import React from 'react';
import { SkeletonFullPage } from './skeletons';
import { UnifiedBackground } from '../../common/components/SharedTheme';

/**
 * Layout wrapper for admin pages with common loading and error states
 */
const AdminPageLayout = ({ 
  children, 
  loading = false, 
  loadingMore = false,
  hasMore = false,
  onLoadMore,
  title,
  subtitle,
  headerActions
}) => {
  return (
    <div className="min-h-screen pt-40 px-8 pb-8">
      <div className="w-full px-4">
        {/* Header */}
        <div className="mb-8" data-animate>
          <div className="flex justify-between items-center mb-6">
            <div>
              <h1 className="text-4xl font-bold text-white mb-2">{title}</h1>
              {subtitle && <p className="text-gray-300">{subtitle}</p>}
            </div>
            {headerActions && (
              <div className="flex space-x-2">
                {headerActions}
              </div>
            )}
          </div>
        </div>

        {/* Loading state */}
        {loading && !loadingMore && (
          <UnifiedBackground>
            <SkeletonFullPage type="dashboard" />
          </UnifiedBackground>
        )}

        {/* Content */}
        {!loading && children}

        {/* Loading more indicator */}
        {loadingMore && (
          <div className="p-4 text-center text-gray-400">
            Loading more...
          </div>
        )}

        {/* End of list message */}
        {!loading && !loadingMore && !hasMore && (
          <div className="p-4 text-center text-gray-400 border-t border-gray-700">
            You've reached the end of the list
          </div>
        )}
      </div>
    </div>
  );
};

export default AdminPageLayout;
